/*ブロックを追加するには、このファイルを編集追加*/
import { useBlockProps } from '@wordpress/block-editor';


/*ドロップダウンメニュー*/
import { SelectControl } from '@wordpress/components';
import { useEffect, useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { useInstanceId } from '@wordpress/compose';

export default function Edit({ attributes, setAttributes }) {
    const blockProps = useBlockProps();
    const { selectedSlug } = attributes;
    const [ posts, setPosts ] = useState([]);
    const instanceId = useInstanceId(Edit);

    useEffect( () => {
        apiFetch( { path: '/wp/v2/modal' } )
            .then( fetchedPosts => {
                setPosts( fetchedPosts );
                if (fetchedPosts.length > 0 && !selectedSlug) {
                    // If no post is selected, select the first (most recent) one
                    setAttributes({ selectedSlug: fetchedPosts[0].slug });
                }
            } );
    }, [] );

    return (
        <div { ...blockProps }>
            <SelectControl
                id={ `my-plugin-select-${instanceId}` }
                label="モーダル画面を選択："
                value={ selectedSlug }
                options={ posts.map( post => ( { label: post.title.rendered, value: post.slug } ) ) }
                onChange={ ( slug ) => { setAttributes( { selectedSlug: slug } ) } }
            />
        </div>
    );
}