/*一意の名前と、その動作を定義するオブジェクトを指定して、新しいブロックを登録する。*/
import { registerBlockType } from '@wordpress/blocks';

/*scss*/
import './style.scss';
import './editor.scss';

/*内部依存関係*/
import Edit from './edit';
import save from './save';
import metadata from './block.json';


/*新しいブロックタイプ定義*/
registerBlockType( metadata.name, {
	example: {
		attributes: {
			message: 'モーダル',
		},
	},
	/**
	 * @see ./edit.js
	 */
	edit: Edit,

	/**
	 * @see ./save.js
	 */
	save,
} );


